.privacy-policy{padding:95px 25px;background-color:#f0f0f0;color:#1d1d1b;font-family:Arial,sans-serif;max-width:1200px;margin:0 auto;border-radius:8px;box-shadow:0px 4px 8px rgba(0,0,0,.1)}.privacy-policy .title{text-align:center;color:#dc052b;font-size:2.5rem;margin-bottom:1.5rem;font-weight:bold}.privacy-policy h1,.privacy-policy h2{color:#dc052b;font-weight:bold}.privacy-policy h1{font-size:2rem;margin-bottom:1.5rem;text-align:center}.privacy-policy h2{font-size:1.6rem;margin-top:2rem;text-transform:uppercase}.privacy-policy p{line-height:1.6;margin:1rem 0;color:#303030}.privacy-policy ul{margin-left:1.5rem;list-style-type:disc;padding-left:0}.privacy-policy ul li{margin:.5rem 0;font-size:1.1rem;color:#1d1d1b;position:relative;padding-left:25px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}.privacy-policy ul li::before{content:"•";color:#dc052b;position:absolute;left:0;top:0;font-size:1.3rem}@media(max-width: 768px){.privacy-policy{padding:20px;max-width:100%}}